/* CSS Reset */
* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}
body {
    margin: 0;
    padding: 0;
    background-color: #121212;
    font-family: Arial, sans-serif;
}

.task-essential {
    padding: 0 5%;
    margin: 0;
    margin-top: 4px;
}


.ep {
    display: flex;
    justify-content: space-between;
    margin-bottom: 10px;
    padding: 12px;
    background-color: #6c63ff;
    border-radius: 8px;
    box-shadow: 0 2px 5px rgba(0, 0, 0, 0.2);
    transition: all 0.3s ease-in-out;
}
.task-video{
    overflow-y: scroll;
}
.task-helper{
    display: flex;
    margin-bottom: 10px;
    width: 100%;
    justify-content: space-between;
    padding: 12px;
    background-color: #6c63ff;
    border-radius: 8px;
    box-shadow: 0 2px 5px rgba(0, 0, 0, 0.2);
    transition: all 0.3s ease-in-out;
}
.ep:hover {
    transform: scale(1.02);
}
.buttons
h3 {
    color: white;
    font-size: 1.2rem;
}



.essential:hover {
    background-color: #444;
}

/* Mobile Styles */
@media (max-width: 768px) {
    .task-heading h1 {
        font-size: 1.5rem;
    }

    .inf {
        font-size: 1rem;
    }

    .ep {
        flex-direction: column;
        align-items: flex-start;
        margin-bottom: 15px;
    }

    .ep h3 {
        font-size: 1.1rem;
        margin-bottom: 10px;
    }

    .essential {
        font-size: 0.8rem;
        padding: 10px 15px;
    }
}

/* Extra small screens (Mobile phones) */
@media (max-width: 480px) {
    body {
        font-size: 14px;
    }

    .task-heading h1 {
        font-size: 1.2rem;
    }

    .inf {
        font-size: 0.9rem;
    }

    .ep {
        padding: 10px;
    }

    .essential {
        font-size: 0.75rem;
        padding: 8px 12px;
    }
}