
#root {
  max-width: 1000px;
  margin: 0;
  height: 100vh;
}

body {
  font-family: Arial, sans-serif;
  margin: 0;
  padding: 0;
  background-color: black;
  display: flex;
  flex-direction: column;
  height: 590px;
  width: 100%;
}

nav {
  position: fixed;
  bottom: 0;
  left: 0;
  width: 100%;
  background-color: black;
  border-top:solid 0.5px white; ;
  padding: 10px 0;
  display: flex;
  justify-content: center;
  align-items: center;
  color: white;
}

nav ul {
  display: flex;
  list-style-type: none;
  margin: 0;
  padding: 0;
}

nav ul li {
  margin: 0 20px;
  justify-content: center;
  align-items: center;
}

nav ul li a svg {
  color: #6c63ff;
  margin-left: 18%;
}
.g-box{
  justify-content: center;
  align-items: center;
  margin-top: 48%;
}

.general-container {
  display: flex;
  flex-direction: column;
  min-height: 100vh;
  text-align: center;
  margin-top: 10%;
}

.home-container {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  margin-bottom: 4%;
  margin-top: 8%;
  /* Add some space between home and join section */
}

.username {
  background-color: #6c63ff;
  height: 9em;
  width: 9em;
  border-radius: 50%;
  display: flex;
  justify-content: center;
  align-items: center;
  border: solid white 4px;
  margin-top: 16%;
  margin-bottom: 4%;
}

.title {
  font-size: 2em;
  line-height: 1;
  margin: 0;
}

.name,
.balance {
  margin-top: 1rem;
  /* Aligning content for better consistency */
}

.clant {
  font-size: 1.5em;
}

.coin {
  font-size: 3rem;
}

#coin {
  font-size: 1.5rem;
}
.join-general{
  margin-left: 0;
}
.join {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  margin-top: 1rem;
}

.join button {
  display: flex;
  align-items: flex-start;
  justify-content: start;
  padding: 10px 15px;
  background-color: #6c63ff;
  color: white;
  border: none;
  border-radius: 5px;
  cursor: pointer;
}


.join button a {
  color: inherit;
  text-decoration: none;
  display: flex;
  align-items: center;
  gap: 0.2rem;
}

.join button:hover {
  background-color: #5848c2;
  /* Darker hover color */
}

/* Button styling */
.alone {
  background-color: #6c63ff;
  border: none;
  width: 16em;
  padding: 20px 20px;
  border-radius: 4px;
  margin-top: 20%;
}

/* Streak container */
.streak-container {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  margin-top: 20%;
}
.streak-container h1{
  font-size: 2rem;
}
/* Login streak styling */
#login-streak {
  margin-top: 20%;
}
.streak-container button{
  margin-top: 60%;
  padding: 12px;
  width: 100%;
}
.referral-container{
  display: flex;
  flex-direction: column;
  padding: 10px;
  font-family: Arial,sans-serif ;
}
.referral-container h2{
  font-size: 2rem;
  margin-top: 10%;
}
.referral-container p{
  font-size: 1rem;
  margin-top: 5%;
  margin-bottom: 2.5%;
}
.referral-container h4{
  font-size: 1rem;
  margin-top: 5%;
  margin-bottom:5%;
}
.referral-link{
  width: 100%;
  padding: 8px;
  font-size: 14px;
}
.copytoclipboard{
  color: white;
  padding: 8px 12px;
  background-color: #007BFF;
  cursor: pointer;
  border: none;
  margin-top: 2%;
}
.refer{
  display: flex;
  flex-direction: column;
  width: 100% ;
  padding-left: 2px;
  margin: 0;
  background-color: black;
  color: white;
}
.refer ul{
  list-style-type: none;
  border: 1px solid white;
}
.refer ul li {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 5px;
  padding: 5px;
  border-radius: 5px;
  background-color: black;
}
.some-tasks {
  margin-top: 10%;
  display: flex;
  flex-direction: column;
}
.task-heading {
  text-align: center;
  margin-bottom: 10px;
  margin-top: 10px;
}
.task {
  font-size: 2rem;
  color: white;
  margin: 0;
}
.info {
  text-align: center;
  margin-bottom: 30px;
  margin-top: 15px;
}
.inf {
  font-size: 1rem;
  color: white;
}
.task-buttons{
  display: flex;
  justify-content: space-around;
  background-color: black;
  align-items: center;
  padding-top: 4px;
  padding-bottom: 4px;
  width: 100%;
  border-radius: 4px;
}
.task-buttons button {
  padding: 8px 12px;
  font-size: 0.9rem;
  font-weight: bold;
  background-color: black;
  color: white;
  border-radius: 5px;
  border: none;
  cursor: pointer;
}
.modal{
  display: flex;
  justify-content: center;
  align-items: center;
}
.modal-content{
  margin-top: 10px;
  margin-bottom: 10px;
  text-align: center;
}
.modal-content h3{
  margin-top: 4px;
  margin-bottom: 2px;
}
.modal-content p{
  margin-top: 4px;
  margin-bottom: 8px;
}
.wallet-container{
  margin-top: 10%;
  display: flex;
  justify-content: center;
  align-items: center;
}
.wallet-container h3{
  margin-top: 40%;
  font-size: 1.5rem;
}